.table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 80%;
  padding: 1rem;
  margin: 1rem;
}

.td, .th {
  border: 1px solid #dddddd;
  text-align: center;
  padding: 1rem;


}

.tr:nth-child(odd){
  background-color: #77C7A7;

}
.tr:nth-child(even) {
  background-color: #23674c;
}

.primary__title{
  font-size: 2rem;
  font-weight: 900;
  text-align: center;
  color: rgb(244, 169, 50) ;
}

.table__container{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
 
}

form{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 300px;
  min-height: 55vh;
  height: 80%;
  width: 60%;
padding: 1rem;
  /* From https://css.glass */
background: rgba(255, 255, 255, 0.2);
border-radius: 16px;
box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
backdrop-filter: blur(5px);
-webkit-backdrop-filter: blur(5px);
border: 1px solid rgba(255, 255, 255, 0.3);
}

input{
  width: 90%;
  margin: 1rem;
  padding: .8rem;
  outline: none;
  font-size: 1.1rem;
}

.screen{
  min-height: 90vh;
  background-color: #081e16;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

label{
  width: 90%;
  color: rgb(244, 169, 50);
  font-weight: 600;
  font-size: 1.1rem;
  margin-top: 1rem;
}

.submit-btn{
  width: 300px;
  margin-top: 2rem;

 padding: 1rem;
 margin-left: auto;
 margin-right:3rem;
}

form h3{
  color: #fff;
  font-size: 2rem;
}

.banner-hero{
  background: rgba(54, 246, 224, 0.25);
box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
backdrop-filter: blur( 4px );
-webkit-backdrop-filter: blur( 4px );
border-radius: 10px;
border: 1px solid rgba( 255, 255, 255, 0.18 );
  min-height: 85vh;
 width: 95%;
 margin: 1rem;
 margin-top: 2rem;
}